import * as React from 'react';
import './ModalResults.css';
import { EXAM_RESULTS_RANGE } from '../../constants';
import { isInRange, toFarsiNumber } from '../../utils';
import questionsJSON from '../../../content/questions.json';
import { Link, navigate } from 'gatsby';
import { GoHome } from "react-icons/go";

const ModalResults = ({ questions }) => {
    const { length: correctAnswersCount } = questions.filter(question => !question.hasError);
    const correctPercentage = (correctAnswersCount / questions.length * 100);
    const wrongAnswers = questions.filter(question => question.hasError);
    const { color } = EXAM_RESULTS_RANGE.find(range => isInRange(correctPercentage, range));
    React.useEffect(() => {
        document.querySelector('meta[name="theme-color"]').setAttribute('content',  color);
    }, []);

    const renderText = () => {
        if (correctPercentage === 100) return " !        "
        else if (correctPercentage === 0) return "         ."
        else if (correctPercentage >= EXAM_RESULTS_RANGE[EXAM_RESULTS_RANGE.length - 1].start) {
            return ` !   ${toFarsiNumber(correctAnswersCount)}   ${toFarsiNumber(questions.length)}    `;
        }
        else if (correctPercentage >= EXAM_RESULTS_RANGE[EXAM_RESULTS_RANGE.length - 2].start) {
            return `  !   ${toFarsiNumber(correctAnswersCount)}   ${toFarsiNumber(questions.length)}    `;
        }
        else return ` !   ${toFarsiNumber(correctAnswersCount)}   ${toFarsiNumber(questions.length)}    `;
    };

    const renderDescription = () => {
        if (correctPercentage === 100) return `    ${toFarsiNumber(questions.length)}    ${toFarsiNumber(questionsJSON.list.length)}       .          :)         .`;
        else if (correctPercentage >= EXAM_RESULTS_RANGE[EXAM_RESULTS_RANGE.length - 1].start) return `            . ${wrongAnswers.length === 1 ? '' : ''}    ${wrongAnswers.length === 1 ? '' : ''}       ${wrongAnswers.length === 1 ? ' ' : ''}.`
        else return '            .            .'
    };

    return (
        <div style={{ background: color }} className='ModalResults'>
            <h1>{renderText()}</h1>
            <h2> : {toFarsiNumber((correctPercentage === 100) || (correctPercentage === 0) ? correctPercentage : correctPercentage.toFixed(1))}</h2>
            <p>{renderDescription()}</p>
            <Link to="/" className='ModalResults__button'><GoHome size="2.3rem" />  </Link>
        </div>
    );
};

export default ModalResults;
