import * as React from 'react';
import Rodal from 'rodal';
import 'rodal/lib/rodal.css';
import './ModalInfo.css';
import { IoCloseOutline } from "react-icons/io5";

const ModalInfo = ({ isVisible, toggle }) => {
    return (
        <Rodal visible={isVisible} leaveAnimation='door' onClose={toggle} showCloseButton={false}>
        <div className='ModalInfo'>
            <div className='ModalInfo__titleContainer'>
                <div>
                    <button aria-label='' className="ModalInfo__closeIconButton" onClick={toggle}>
                        <IoCloseOutline color='var(--color-text-primary)' size="3rem" />
                    </button>
                </div>
                <h1> </h1>
                <div />
            </div>
            <p> !                   .                        .                 .         -    -  .</p>
            <p>    PWA (<a rel="noreferrer" href='https://github.com/Sadegh-Zr/fallacies' target='_blank'>  </a>)                    .</p>
            <p>        <a rel="noreferrer" href='mailto:sadegh.zarinmehr@gmail.com' target='_blank'></a>  <a rel="noreferrer" href='https://eitaa.com/sadegh_zr' target='_blank'> </a>     .</p>
            <button aria-label=' !' className='ModalInfo__close' onClick={toggle}> !</button>
        </div>
    </Rodal>
    )
};

export default ModalInfo;
